export default {
	data() {
		return {
			orderArr: [{
				title: '线路订单',
				type: 1
			}, {
				title: '茶咖订单',
				type: 2
			}, {
				title: '产品订单',
				type: 3
			}],
			orderType: 1
		}
	},
	methods: {
		toCutOrderType(idx) {
			this.orderArr.map((v, i) => {
				if(idx == i) {
					this.orderType = v.type
				}
			})
		}
	}
}
